<template lang="html">
  <div class="login-box">
    <vue-headful
      :title="$route.meta.title"
    />
    <div v-if="success" class="success-msg">
      <span class="tmcu-text"> {{ ruleForm.email }}</span>
      <span class="tmcu-btn" @click="tryAgain">?</span>
    </div>
    <div v-else>
      <p class="login-title"></p>
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        @keyup.enter.native="submitForm('ruleForm')"
      >
        <el-form-item label="" prop="email">
          <el-input v-model="ruleForm.email" auto-complete="on" />
        </el-form-item>
        <el-form-item class="captcha-form-item" label="" prop="captcha">
          <el-input class="captcha-input" v-model="ruleForm.captcha" />
          <span @click="getCaptchaSVG" v-html="captchaSVG"></span>
        </el-form-item>
        <el-form-item>
          <el-button @click="resetForm('ruleForm')"></el-button>
          <el-button
            class="submit-btn"
            type="primary"
            :loading="loading"
            @click="submitForm('ruleForm')"
          ></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import utils from '@/utils';
import { forgotPass, checkEmailExist, getCaptcha, checkCaptcha } from './api';

export default {
  name: 'ForgotPassword',
  created() {
    this.getCaptchaSVG();
  },
  data() {
    const checkEmail = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''));
      } else if (!utils.validEmail(value)) {
        callback(new Error(''));
      } else {
        checkEmailExist(value).then(() => {
          callback(new Error(''));
        }, () => {
          callback();
        });
      }
    };
    const validateCaptcha = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (!utils.validCaptcha(value)) {
        callback(new Error(''));
      } else {
        checkCaptcha(value).then(() => {
          callback();
        }).catch((error) => {
          callback(new Error(error));
        });
      }
    };
    return {
      success: false,
      ruleForm: {
        email: '',
        captcha: '',
      },
      rules: {
        email: [
          { required: true, validator: checkEmail, trigger: 'blur' },
        ],
        captcha: [
          { required: true, validator: validateCaptcha, trigger: 'blur' },
        ],
      },
      captchaSVG: '',
      loading: false,
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true;
          forgotPass({
            email: this.ruleForm.email,
            captcha: this.ruleForm.captcha,
            host: window.location.host,
          }).then(() => {
            this.loading = false;
            this.success = true;
          }).catch((error) => {
            this.loading = false;
            this.$message.error(error);
            this.getCaptchaSVG();
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getCaptchaSVG() {
      getCaptcha().then((svg) => {
        this.captchaSVG = svg;
      }).catch((error) => {
        this.captchaSVG = '';
        this.$message.error(error);
      });
    },
    tryAgain() {
      this.success = false;
      this.ruleForm.email = '';
      this.ruleForm.captcha = '';
      this.getCaptchaSVG();
    },
  },
};
</script>

<style lang="css" scoped>
.login-title {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  padding-bottom: 20px;
}

.login-box {
  width: 500px;
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 0 4px 2.4rem -0.4rem #9da5ab;
  padding: 20px 30px 1px 0;
}

.submit-btn {
  float: right;
}

.captcha-input {
  width: 50%;
  vertical-align: top;
}
</style>

<style lang="css">
.captcha-form-item > .el-form-item__content {
  line-height: normal;
}

.success-msg {
  text-align: center;
  margin-bottom: 20px;
}
</style>



