import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/containers/Home';
import RegistPage from '@/containers/LoginRegist/RegistPage';
import LoginPage from '@/containers/LoginRegist/LoginPage';
import ForgotPassword from '@/containers/LoginRegist/ForgotPassword';
import ResetPassword from '@/containers/LoginRegist/ResetPassword';
import ActivateEmail from '@/containers/LoginRegist/ActivateEmail';
import ClassPage from '@/containers/ClassPage';
import NotFound from '@/containers/NotFound';
import HomeworkPage from '@/containers/HomeworkPage';

Vue.use(Router);

/*
 * meta.auth
 * 0 - 
 * 1 - 
 * 2 - 
*/

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '',
        auth: 2,
      },
    },
    {
      path: '/regist',
      name: 'RegistPage',
      component: RegistPage,
      meta: {
        title: ' | ',
        auth: 1,
      },
    },
    {
      path: '/login',
      name: 'LoginPage',
      component: LoginPage,
      meta: {
        title: ' | ',
        auth: 1,
      },
    },
    {
      path: '/forgot',
      name: 'ForgotPassword',
      component: ForgotPassword,
      meta: {
        title: ' | ',
        auth: 1,
      },
    },
    {
      path: '/reset/:resetId([a-f0-9]{24})',
      name: 'ResetPassword',
      component: ResetPassword,
      meta: {
        title: ' | ',
        auth: 1,
      },
    },
    {
      path: '/activate/:activateId([a-f0-9]{24})',
      name: 'ActivateEmail',
      component: ActivateEmail,
      meta: {
        title: ' | ',
        auth: 1,
      },
    },
    {
      path: '/class/:classId',
      name: 'ClassPage',
      component: ClassPage,
      meta: {
        title: ' | ',
        auth: 2,
      },
    },
    {
      path: '/hw/:classId/:createDate([0-9]{13})',
      name: 'HomeworkPage',
      component: HomeworkPage,
      meta: {
        title: ' | ',
        auth: 2,
      },
    },
    {
      path: '/*',
      component: NotFound,
      meta: {
        title: ' | ',
        auth: 0,
      },
    },
  ],
  scrollBehavior() {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({ x: 0, y: 0 });
      }, 0);
    });
  },
});



