<template lang="html">
  <div>
    <vue-headful
      :title="$route.meta.title"
    />
    <page-loading v-if="loading || error" :loading="loading" :error="error" />
    <div v-if="!loading && !error && $store.state.user.email && homework.createDate">
      <div class="hw-box">
        <div class="custom-card">
          <p>
            <breadcrumb
              class="breadcrumb"
              :paths="[
                { name: '', to: '/' },
                { name: '', to: `/class/${$route.params.classId}` },
                { name: '' },
              ]"
            />
            <span class="hw-title">{{ homework.title }}</span>
            <span :class="`hw-stauts-${expired}`">{{ expired | hwStatus }}</span>
            <span v-if="$store.state.user.role === 'teacher'">
              <el-button type="text" @click="showEditHomeworkDialog()">
                <span></span>
                <i class="el-icon-edit"></i>
              </el-button>
            </span>
            <span class="hw-answer tmcu-btn" @click="showHwAnswerDialog()">
              {{ $store.state.user.role |
                answerStatus(homework.hwAnswer.answer, homework.hwAnswer.files.length, isTA) }}
            </span>
          </p>
          <p>
            <span class="hw-info tmcu-text">: {{ formateDate(homework.createDate) }}</span>
            <span class="hw-info tmcu-text">: {{ formateDate(homework.beginDate) }}</span>
            <span class="hw-info tmcu-text">: {{ formateDate(homework.endDate) }}</span>
          </p>
        </div>
        <markdown-editor
          :value="homework.description"
          :edit="false"
        />
        <file-list :files="homework.files" />
      </div>
      <div v-if="$store.state.user.role === 'teacher' || isTA">
        <p class="tmcu-text"> {{ homework.submissions.length }} </p>
        <submissions-table
          :submissions="homework.submissions"
          :classId="$route.params.classId"
          :createDate="homework.createDate"
          @fetchData="fetchData"
        />
      </div>
      <div v-if="$store.state.user.role === 'student' && !isTA">
        <div v-if="homework.submissions.length">
          <p class="tmcu-text">
            <el-tag size="small"></el-tag>
            <span v-if="!homework.submissions[0].checked && expired === 2">
              <el-button type="text" @click="editingSub = homework.submissions[0]"></el-button>
            </span>
            <span v-if="!homework.submissions[0].checked && expired === 2">
              <el-button type="text" :loading="cancelLoading" @click="deleteHwSub"></el-button>
            </span>
          </p>
          <p class="tmcu-text">
            <span>: {{ formateDate(homework.submissions[0].date) }}</span>
          </p>
          <div class="tmcu-text">
            <p v-if="homework.submissions[0].checked">
              <el-tag size="small"></el-tag>
              <span>: {{ homework.submissions[0].feedback || '' }}</span>
            </p>
            <p v-else><el-tag type="warning" size="small"></el-tag></p>
          </div>
          <div v-if="editingSub">
            <p>
              <span class="tmcu-text"></span>
              <el-button type="text" @click="editingSub = null"></el-button>
            </p>
            <br />
            <submit-homework
              :classId="$route.params.classId"
              :createDate="homework.createDate"
              :text="editingSub.answer"
              :files="editingSub.files"
              :editing="true"
              :draft="homework.draft"
              @fetchData="fetchData"
              @fetchDataByDraft="fetchDataByDraft"
            />
          </div>
          <div v-else>
            <p class="tmcu-text">:</p>
            <br />
            <markdown-editor
              :value="homework.submissions[0].answer"
              :edit="false"
            />
            <file-list :files="homework.submissions[0].files" />
          </div>
        </div>
        <div v-else>
          <p><el-tag type="danger" size="small"></el-tag></p>
          <br />
          <submit-homework
            v-if="expired === 2"
            :classId="$route.params.classId"
            :createDate="homework.createDate"
            text=""
            :files="[]"
            :editing="false"
            :draft="homework.draft"
            @fetchData="fetchData"
            @fetchDataByDraft="fetchDataByDraft"
          />
        </div>
      </div>
      <hw-answer-dialog
        ref="hwAnswerDialogRef"
        :classId="$route.params.classId"
        :createDate="homework.createDate"
        :isTA="isTA"
        @fetchData="fetchData"
      />
      <edit-homework-dialog
        ref="editHomeworkDialogRef"
        :classId="$route.params.classId"
        :createDate="homework.createDate"
        @fetchData="fetchData"
      />
    </div>
  </div>
</template>

<script>
import MarkdownEditor from '@/components/MarkdownEditor';
import FileList from '@/components/FileList';
import PageLoading from '@/components/PageLoading';
import Breadcrumb from '@/components/Breadcrumb';
import utils from '@/utils';
import { getHwSubsData, deleteHwSubApi } from './api';
import SubmissionsTable from './components/SubmissionsTable';
import SubmitHomework from './components/SubmitHomework';
import HwAnswerDialog from './components/HwAnswerDialog';
import EditHomeworkDialog from './components/EditHomeworkDialog';

export default {
  name: 'HomeworkPage',
  created() {
    this.fetchData();
  },
  components: {
    SubmissionsTable,
    SubmitHomework,
    MarkdownEditor,
    FileList,
    HwAnswerDialog,
    EditHomeworkDialog,
    PageLoading,
    Breadcrumb,
  },
  computed: {
    expired() {
      if (this.homework.beginDate && this.homework.beginDate > Date.now()) {
        return 0;
      } else if (this.homework.endDate && this.homework.endDate < Date.now()) {
        return 1;
      }
      return 2;
    },
    isTA() {
      const userEmail = this.$store.state.user.email;
      for (let i = 0; i < this.homework.tas.length; i += 1) {
        if (this.homework.tas[i].email === userEmail) return true;
      }
      return false;
    },
  },
  data() {
    return {
      homework: {
        createDate: '',
        beginDate: '',
        endDate: '',
        title: '',
        description: '',
        submissions: [],
        files: [],
        hwAnswer: {
          answer: '',
          files: [],
        },
        tas: [],
        draft: null,
      },
      editingSub: null,
      cancelLoading: false,
      loading: false,
      error: false,
    };
  },
  methods: {
    fetchData() {
      this.loading = true;
      this.error = false;
      getHwSubsData(this.$route.params.classId, this.$route.params.createDate)
        .then((data) => {
          this.loading = false;
          this.homework = data;
          this.editingSub = null;
        })
        .catch((error) => {
          this.loading = false;
          this.error = true;
          this.$message.error(error);
        });
    },
    fetchDataByDraft() {
      getHwSubsData(this.$route.params.classId, this.$route.params.createDate)
        .then((data) => {
          this.homework = data;
        })
        .catch((error) => {
          this.$message.error(error);
        });
    },
    deleteHwSub() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        this.cancelLoading = true;
        deleteHwSubApi(this.$route.params.classId, this.$route.params.createDate)
          .then(() => {
            this.cancelLoading = false;
            this.fetchData();
          })
          .catch((error) => {
            this.cancelLoading = false;
            this.$message.error(error);
          });
      }).catch(() => {});
    },
    formateDate(timestamp) {
      return timestamp ? utils.formateDate(+timestamp) : '--';
    },
    showHwAnswerDialog() {
      this.$refs.hwAnswerDialogRef.show(this.homework.hwAnswer);
    },
    showEditHomeworkDialog() {
      this.$refs.editHomeworkDialogRef.show(this.homework);
    },
  },
  filters: {
    hwStatus(val) {
      if (val === 0) {
        return '';
      } else if (val === 1) {
        return '';
      }
      return '';
    },
    answerStatus(role, answer, length, isTA) {
      if (answer || length) {
        return '';
      } else if (role === 'student' && !isTA) {
        return '';
      }
      return '';
    },
  },
};
</script>

<style lang="css" scoped>
.custom-card {
  background-color: #fff;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  border: 1px solid #ebeef5;
  border-radius: 4px;
  overflow: hidden;
  padding: 10px;
  margin-bottom: 20px;
  text-align: center;
}

.breadcrumb {
  float: left;
}

.hw-box {
  margin-bottom: 20px;
}

.hw-title {
  color: #606266;
  font-size: 20px;
  font-weight: bold;
}

.hw-info {
  padding-left: 10px;
}

.hw-stauts-0 {
  border: 1px solid #E6A23C;
  border-radius: 3px;
  font-size: 14px;
  color: #E6A23C;
  padding: 2px 3px;
}

.hw-stauts-1 {
  border: 1px solid #909399;
  border-radius: 3px;
  font-size: 14px;
  color: #909399;
  padding: 2px 3px;
}

.hw-stauts-2 {
  border: 1px solid #67C23A;
  border-radius: 3px;
  font-size: 14px;
  color: #67C23A;
  padding: 2px 3px;
}

.hw-answer {
  float: right;
}
</style>



