<template lang="html">
  <fullscreen-dialog
    :title="`${submission.stuName}(${submission.stuId})`"
    :visible="visible"
    @close="closeDialog"
  >
    <div>
      <p v-if="submission.checked">
        <el-tag size="small"></el-tag>
        <span>: {{ submission.feedback || '' }}</span>
      </p>
      <p v-else><el-tag type="warning" size="small"></el-tag></p>
    </div>
    <br />
    <p>: {{ formateDate(submission.date) }}</p>
    <div>
      <markdown-editor
        :value="submission.answer"
        :edit="false"
      />
      <file-list :files="submission.files" />
    </div>
    <br />
    <div v-if="!submission.checked">
      <div>
        <p>:</p>
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 10}"
          placeholder=""
          v-model="feedback">
        </el-input>
      </div>
      <br />
      <div class="submit-btn">
        <el-button type="primary" :loading="loading" @click="submitFeedback"></el-button>
      </div>
    </div>
  </fullscreen-dialog>
</template>

<script>
import MarkdownEditor from '@/components/MarkdownEditor';
import FileList from '@/components/FileList';
import FullscreenDialog from '@/components/FullscreenDialog';
import utils from '@/utils';
import { checkStuSub } from '../api';

export default {
  name: 'ShowStuSubDialog',
  props: {
    classId: {
      type: String,
      default: '',
    },
    createDate: {
      type: String,
      default: Date.now(),
    },
  },
  components: {
    MarkdownEditor,
    FileList,
    FullscreenDialog,
  },
  data() {
    return {
      visible: false,
      submission: {
        userId: '',
        stuId: '',
        stuName: '',
        email: '',
        date: '',
        answer: '',
        file: '',
        checked: false,
        feedback: '',
      },
      feedback: '',
      loading: false,
    };
  },
  methods: {
    closeDialog() {
      this.visible = false;
    },
    show(row) {
      this.visible = true;
      this.submission.userId = row.userId;
      this.submission.stuId = row.stuId;
      this.submission.stuName = row.stuName;
      this.submission.email = row.email;
      this.submission.date = row.date;
      this.submission.answer = row.answer;
      this.submission.files = row.files;
      this.submission.checked = row.checked;
      this.submission.feedback = row.feedback;
    },
    submitFeedback() {
      this.loading = true;
      checkStuSub(
        this.classId,
        this.createDate,
        this.submission.userId,
        this.feedback,
      ).then(() => {
        this.loading = false;
        this.visible = false;
        this.$emit('fetchData');
      }).catch((error) => {
        this.loading = false;
        this.$message.error(error);
      });
    },
    formateDate(timestamp) {
      return timestamp ? utils.formateDate(+timestamp) : '--';
    },
  },
};
</script>

<style lang="css" scoped>
</style>



