<template lang="html">
  <div class="login-box">
    <vue-headful
      :title="$route.meta.title"
    />
    <p class="login-title"></p>
    <el-form
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      @keyup.enter.native="submitForm('ruleForm')"
    >
      <el-form-item label="" prop="email">
        <el-input v-model="ruleForm.email" auto-complete="on" />
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="ruleForm.password" />
      </el-form-item>
      <el-form-item class="captcha-form-item" label="" prop="captcha">
        <el-input class="captcha-input" v-model="ruleForm.captcha" />
        <span @click="getCaptchaSVG" v-html="captchaSVG"></span>
      </el-form-item>
      <el-form-item>
        <el-button @click="resetForm('ruleForm')"></el-button>
        <el-button
          class="submit-btn"
          type="primary"
          :loading="loading"
          @click="submitForm('ruleForm')"
        ></el-button>
      </el-form-item>
      <el-form-item>
        <router-link class="tmcu-btn" to="/forgot">?</router-link>
        <span v-if="emailNotActivate" class="email-not-activate">
          <span class="email-not-activate-warnning"></span>
          <el-button
            type="text"
            :loading="activateLoading"
            @click="activateNow"
          ></el-button>
        </span>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import utils from '@/utils';
import { loginUser, checkEmailExist, getCaptcha, checkCaptcha, sendEmailAgain } from './api';

export default {
  name: 'LoginPage',
  created() {
    this.getCaptchaSVG();
  },
  data() {
    const checkEmail = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''));
      } else if (!utils.validEmail(value)) {
        callback(new Error(''));
      } else {
        checkEmailExist(value).then(() => {
          callback(new Error(''));
        }, () => {
          callback();
        });
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    const validateCaptcha = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (!utils.validCaptcha(value)) {
        callback(new Error(''));
      } else {
        checkCaptcha(value).then(() => {
          callback();
        }).catch((error) => {
          callback(new Error(error));
        });
      }
    };
    return {
      ruleForm: {
        email: '',
        password: '',
        captcha: '',
      },
      rules: {
        email: [
          { required: true, validator: checkEmail, trigger: 'blur' },
        ],
        password: [
          { required: true, validator: validatePass, trigger: 'blur' },
        ],
        captcha: [
          { required: true, validator: validateCaptcha, trigger: 'blur' },
        ],
      },
      captchaSVG: '',
      emailNotActivate: false,
      loading: false,
      activateLoading: false,
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true;
          loginUser({
            email: this.ruleForm.email,
            password: this.ruleForm.password,
            captcha: this.ruleForm.captcha,
          }).then(() => {
            this.loading = false;
            this.$store.dispatch('refreshHeader').then(() => {
              this.$router.go(-1);
            });
          }).catch((error) => {
            this.loading = false;
            if (error === '') {
              this.emailNotActivate = true;
            } else {
              this.$message.error(error);
              this.getCaptchaSVG();
            }
          });
        }
      });
    },
    activateNow() {
      this.activateLoading = true;
      sendEmailAgain({
        email: this.ruleForm.email,
        host: window.location.host,
      }).then(() => {
        this.activateLoading = false;
        this.emailNotActivate = false;
        this.$message.success(` ${this.ruleForm.email} `);
      }).catch((error) => {
        this.activateLoading = false;
        this.$message.error(error);
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getCaptchaSVG() {
      getCaptcha().then((svg) => {
        this.captchaSVG = svg;
      }).catch((error) => {
        this.captchaSVG = '';
        this.$message.error(error);
      });
    },
  },
};
</script>

<style lang="css" scoped>
.login-title {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  padding-bottom: 20px;
}

.login-box {
  width: 500px;
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 0 4px 2.4rem -0.4rem #9da5ab;
  padding: 20px 30px 1px 0;
}

.submit-btn {
  float: right;
}

.captcha-input {
  width: 50%;
  vertical-align: top;
}

.email-not-activate {
  float: right;
}

.email-not-activate-warnning {
  color: #f56c6c;
  font-size: 14px;
}
</style>

<style lang="css">
.captcha-form-item > .el-form-item__content {
  line-height: normal;
}
</style>



