<template lang="html">
  <div class="activate-mail">
    <vue-headful
      :title="$route.meta.title"
    />
    <el-button :loading="loading" type="primary" @click="activate"></el-button>
  </div>
</template>

<script>
import { activateEmail } from './api';

export default {
  name: 'ActivateEmail',
  data() {
    return {
      loading: false,
    };
  },
  methods: {
    activate() {
      this.loading = true;
      activateEmail(this.$route.params.activateId).then(() => {
        this.loading = false;
        this.$router.push('/login');
        this.$message.success('');
      }).catch((error) => {
        this.loading = false;
        this.$message.error(error);
      });
    },
  },
};
</script>

<style lang="css" scoped>
.activate-mail {
  text-align: center;
  margin-top: 200px;
}
</style>



