<template lang="html">
  <div>
    <div class="edit" v-if="edit">
      <mavon-editor
        :toolbars="toolbars"
        :value="value"
        @change="val => $emit('change', val)"
      />
    </div>
    <div class="not-edit" v-else>
      <mavon-editor
        :toolbarsFlag="false"
        :subfield="false"
        defaultOpen="preview"
        :value="value"
      />
    </div>
  </div>
</template>

<script>
const toolbars = {
  bold: true, // 
  italic: true, // 
  header: true, // 
  underline: true, // 
  strikethrough: true, // 
  mark: true, // 
  superscript: true, // 
  subscript: true, // 
  quote: true, // 
  ol: true, // 
  ul: true, // 
  link: true, // 
  imagelink: false, // 
  code: true, // code
  table: true, // 
  fullscreen: true, // 
  readmodel: true, // 
  htmlcode: false, // html
  help: true, // 
  /* 1.3.5 */
  undo: true, // 
  redo: true, // 
  trash: true, // 
  save: false, // eventssave
  /* 1.4.2 */
  navigation: true, // 
  /* 2.1.8 */
  alignleft: true, // 
  aligncenter: true, // 
  alignright: true, // 
  /* 2.2.1 */
  subfield: true, // 
  preview: true, // 
};

export default {
  name: 'MarkdownEditor',
  props: {
    edit: {
      type: Boolean,
      default: true,
    },
    value: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      toolbars,
    };
  },
};
</script>

<style lang="css" scoped>
.v-note-wrapper {
  max-height: 800px;
  z-index: 1;
}

.edit .v-note-wrapper {
  min-height: 500px;
}

.not-edit .v-note-wrapper {
  min-height: auto;
}
</style>



